import { ref} from "@vue/composition-api"
import useEvent from './use-event'

export default function (el) {

    const scrollY = ref(null)
    const scrollX = ref(null)
    
    const handleScroll = () => {
      scrollY.value = Math.round(el.value.scrollTop)
      scrollX.value = Math.round(el.value.scrollLeft)
    }
  
    const remove = useEvent(el.value, 'scroll', handleScroll)
  
    return {
      scrollX,
      scrollY,
      remove,
      handleScroll
    }
}


